  font-weight: 400;
  font-style: normal; }

@font-face {
  font-family: 'Space Age Cyrillic Regular';
  font-display: swap;
  src: url("../fonts/spaceagecyrillic-regular.woff2") format("woff2"), url("../fonts/sspaceagecyrillic-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal; }

html {
  font-family: "SF Pro Display", "Arial", sans-serif;
  box-sizing: border-box;
  height: 100%; }

*,
*::before,
*::after {
  box-sizing: inherit;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0); }

body {
  min-width: 320px;
  display: flex;
  flex-direction: column;
  width: 100%;
  min-height: 100vh;
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-style: normal;
  font-size: 18px;
  line-height: 21px;
  color: #ffffff;
  background-color: #0c0e1c;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  scroll-behavior: smooth; }
  body.no-scroll {
    max-height: 100%;
    overflow: hidden;
    padding-right: 17px; }
  body.no-scroll-ios {
    overflow: hidden;
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0; }

a {
  color: inherit; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden,
.form-radio input[type="radio"],
.form-checkbox input[type="checkbox"],
.form-radio-checkbox input[type="radio"],
.form-radio-checkbox input[type="checkbox"] {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

input:invalid {
  outline: none; }

